/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.loginHeader {
  background-color: slategrey;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.loginHeader a {
  cursor: pointer;
  margin-left: 10px;
  padding: 10px;
}
.loginHeader ul {
  display: flex;
  flex-direction: row;
}

.loginHeader ul li {
  cursor: pointer;
  padding: 10px;
}

@media screen and (orientation: portrait) {
  .loginHeader {
    height: 5vmax;
  }
}

@media screen and (orientation: landscape) {
  .loginHeader {
    height: 5vmin;
  }
}

.menuBtn {
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  color: black;
  background-color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  margin: 5px 7.5px;
  padding: 12.5px 62.5px;
}

.login {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  justify-content: flex-start;
}

.id,
.pw {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin: 5px 10px;
}

.btns {
  display: flex;
  justify-content: space-between;
}

.btn {
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  color: black;
  background-color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  margin: 5px 7.5px;
  padding: 12.5px 7.5px;
}

.inputBox {
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 10px;
}

.signUpBtns {
  display: flex;
  justify-content: space-between;
}

.name,
.phone {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin: 5px 10px;
}

.seats {
  border-radius: 10px;
  padding: 20px 5px;
  display: flex;
  background-color: rgb(245, 245, 245, 0.9);
  flex-direction: column;
  margin: 10px;
  align-items: center;
  justify-content: center;
}

.seat {
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  border: black 0.1vmin solid;
  padding: 15px 10px;
}

.seat:hover {
  background: deepskyblue;
}

.seat.userIn {
  background-color: #f80805;
}

.seat.userIn:hover {
  background-color: lightcoral;
}

.blank {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 9vmin;
  height: 9vmin;
}

.row {
  display: flex;
  flex-direction: row;
}

.time,
.date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin: 5px 10px;
}
.time.inputBox,
.date.inputBox {
  width: 70%;
}

.times {
  border-radius: 10px;
  padding: 20px 5px;
  display: flex;
  background-color: rgb(245, 245, 245, 0.9);
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loginFooter {
  background-color: slategrey;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
}

.loginFooter span {
  cursor: pointer;
  margin-left: 10px;
  padding: 10px;
}

@media screen and (orientation: portrait) {
  .loginFooter {
    height: 8vmax;
  }
}

@media screen and (orientation: landscape) {
  .loginFooter {
    height: 8vmin;
  }
}

@media screen and (orientation: portrait) {
  body {
    font-family: sans-serif;
    width: 100vmin;
    height: 100vmax;
  }
}

@media screen and (orientation: landscape) {
  body {
    font-family: sans-serif;
    width: 100vmax;
    height: 100vmin;
  }
}

a {
  text-decoration: none;
  color: white;
}

@media screen and (orientation: portrait) {
  .App {
    height: 87vmax;
  }
}

@media screen and (orientation: landscape) {
  .App {
    height: 87vmin;
  }
}
.App {
  background: linear-gradient(
      to top,
      slategrey,
      rgb(115, 135, 155, 0.5) 1%,
      rgb(115, 135, 155, 0.5) 99%,
      slategrey
    ),
    url(/static/media/book.477be021.jpg);
  background-position: center;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bookingWrap,
.SignUpWrap,
.loginWrap {
  margin: 10px;
  padding: 20px 5px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: rgb(245, 245, 245, 0.9);
}

